import {
  ChangeDetectorRef,
  Directive,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { BehaviorSubject, combineLatest, iif, merge, Observable, of, ReplaySubject, Subject } from 'rxjs';
import { TransactionsBackendError } from '../model/transactions-backend-error.model';
import { TransactionsFilterOptions } from '../model/transactions-filter-options.model';
import { TransactionsList } from '../model/transactions-list.model';
import { LoadingState, TransactionsService } from '../services/transactions.service';
import { EndpointType, PaginationType, WidgetPropertiesService } from '../services/widget-properties.service';
import { TransactionsListOptions } from '../model/transactions-list-options.model';
import { map, mergeMap, publishReplay, refCount, scan, takeUntil, tap } from 'rxjs/operators';
import { Pubsub } from '@backbase/foundation-ang/web-sdk';
import { TransactionsSortOptions } from '../model/transactions-sort-options.model';

import { filterInputParameters, notificationTtl } from '../constants';
import {
  combineExportTypesConfig,
  TransactionsExportConfig,
  TransactionsExportTypeElement,
} from '../model/transactions-export-options.model';
import { PendingTransactionsService } from '../services/pending-transactions.service';
import { TransactionCheckImageResponse } from '../model/transactions-check-images.model';
import { Transaction } from '../model/transaction.model';
import { TransactionDetailsService } from '../services/transaction-details.service';
import { TransactionsDetailsLoading } from '../model/transactions-details.model';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { getNameFromContentDispositionHeader } from '../model/transactions-list-utils.model';
import { HttpErrorResponse } from '@angular/common/http';
import { InquireAndDisputeTopics } from '../model/messages.model';
import { AvailableFilters, AvailableFiltersConfig } from '../model/available-filters.model';
import { AvailableFiltersConfigService } from '../services/available-filters-config.service';
import { NotificationService } from '@backbase/ui-ang/notification';

@Directive()
// eslint-disable-next-line
export class BaseTransactionsContainer implements OnInit, OnDestroy {
  // eslint-disable-next-line
  @ViewChild('notificationErrorUpdateHeader') notificationErrorUpdateHeaderTemplate: TemplateRef<string> | undefined;
  // eslint-disable-next-line
  @ViewChild('notificationErrorUpdateMessage') notificationErrorUpdateMessageTemplate: TemplateRef<string> | undefined;
  // eslint-disable-next-line
  @ViewChild('notificationServerErrorHeader', { static: false })
  notificationServerErrorHeader: TemplateRef<string> | undefined;
  // eslint-disable-next-line
  @ViewChild('notificationServerErrorMessage', { static: false })
  notificationServerErrorMessage: TemplateRef<string> | undefined;
  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;
  private readonly apiKeySubject = new BehaviorSubject<string | undefined>(undefined);
  /**
   * API key needed for the google maps to work.
   */
  // eslint-disable-next-line
  @Input()
  set googleAPIKey(key: string | undefined) {
    this.apiKeySubject.next(key);
  }
  /**
   * Observable of the arrangement ID this component should retrieve the transactions for.
   */
  @Input() selectedAccount: Observable<Array<string>> | undefined;
  /**
   * Observable of options to initialize the filter with.  By default, it is empty.
   */
  @Input() initialFilterOptions: Observable<TransactionsFilterOptions> = of({});
  /**
   * Observable of options to initialize the sort options with.  By default, it is empty.
   */
  @Input() initialSortOptions: Observable<TransactionsSortOptions | undefined> = of(undefined);

  /**
   * input parameters that will take the place of model properties--
   */

  @Input('pageSize') set pageSizeValue(value: number) {
    this.pageSizeSubject.next(value);
  }
  @Input('maxNavPages') set maxNavPagesValue(value: number) {
    this.maxNavPagesSubject.next(value);
  }
  @Input('paginationType') set paginationTypeValue(value: string) {
    this.paginationTypeSubject.next(value as PaginationType);
  }
  @Input('showControls') set showControlsValue(value: boolean) {
    this.showControlsSubject.next(value);
  }
  @Input('showChangeCategory') set showChangeCategoryValue(value: boolean) {
    this.showChangeCategorySubject.next(value);
  }
  @Input('showPendingTransactionsOnTop') set showPendingTransactionsOnTopValue(value: boolean) {
    this.showPendingTransactionsOnTopSubject.next(value);
  }
  @Input('showCheckImage') set showCheckImageValue(value: boolean) {
    this.showCheckImageSubject.next(value);
  }
  @Input('showManageNotes') set showManageNotesValue(value: boolean) {
    this.showManageNotesSubject.next(value);
  }
  @Input('getOrPostEndpoints') set getOrPostEndpointsValue(value: EndpointType) {
    this.getOrPostEndpointsSubject.next(value);
  }
  @Input('disputeByBillingStatus') set disputeByBillingStatusValue(value: string | boolean) {
    this.disputeByBillingStatusSubject.next(typeof value === 'boolean' ? value : value === 'true');
  }
  @Input('disputeEligibilityDays') set disputeEligibilityDaysValue(value: number) {
    this.disputeEligibilityDaysSubject.next(value);
  }
  @Input('disputeTransactionTypes') set disputeTransactionTypesValue(value: string | undefined) {
    this.disputeTransactionTypesSubject.next((value || '').split(','));
  }

  @Input('enableDisputeAndInquiry') set enableDisputeAndInquiryValue(value: string | boolean) {
    this.enableDisputeAndInquirySubject.next(typeof value === 'boolean' ? value : value === 'true');
  }
  @Input('availableFiltersConfig') set availableFiltersValue(availableFiltersConfig: AvailableFiltersConfig) {
    this.availableFiltersConfig.next(availableFiltersConfig);
  }

  @Input('inquireTopicId') inquireTopicIdValue: string | undefined;
  @Input('disputeTopicId') disputeTopicIdValue: string | undefined;

  @Output() transactionSelected = new EventEmitter<any>();
  @Output() transactionsLoaded = new EventEmitter<TransactionsList | null>();

  private pageSizeSubject = new BehaviorSubject<number | undefined>(undefined);
  private maxNavPagesSubject = new BehaviorSubject<number | undefined>(undefined);
  private paginationTypeSubject = new BehaviorSubject<PaginationType | undefined>(undefined);
  private showControlsSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private showChangeCategorySubject = new BehaviorSubject<boolean | undefined>(undefined);
  private showPendingTransactionsOnTopSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private showCheckImageSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private showManageNotesSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private getOrPostEndpointsSubject = new BehaviorSubject<EndpointType | undefined>(undefined);
  private disputeByBillingStatusSubject = new BehaviorSubject<boolean | undefined>(undefined);
  private disputeEligibilityDaysSubject = new BehaviorSubject<number | undefined>(undefined);
  private disputeTransactionTypesSubject = new BehaviorSubject<Array<string> | undefined>(undefined);
  private enableDisputeAndInquirySubject = new BehaviorSubject<boolean>(false);
  private inquireTopicIdSubject = new BehaviorSubject<string>('');
  private disputeTopicIdSubject = new BehaviorSubject<string>('');
  /** end input parameters--- */

  /**
   * Information about the backend error, if it occurs.
   */
  /**
   * @deprecated will be removed in 11.0.0
   */
  get error(): TransactionsBackendError | undefined {
    return this.errors && this.errors[0];
  }

  errors: TransactionsBackendError[] | undefined;
  /**
   * Observable of the transactions list.
   */
  transactionsList: Observable<TransactionsList | null> | undefined;
  /**
   * Observable of the transactions list.
   */
  pendingTransactionsList: Observable<TransactionsList | null> | undefined;
  /**
   * Observable of the current page.
   */
  currentPage: Observable<number> | undefined;
  /**
   * Observable of the loading state of the service.
   */
  loadingState: Observable<LoadingState> | undefined;
  /**
   * Observable of the loading state of the pending transactions service.
   */
  loadingPendingState: Observable<LoadingState> | undefined;
  loadingDetails: Observable<TransactionsDetailsLoading> | undefined;
  /**
   * Events registered to transactions widgets
   */
  private registeredRefreshTransactionsListEvents: string[] = [];
  private unsubscribeSubject = new Subject<void>();
  /**
   * Subject that listens the transactionsList observable and provides the export types
   */
  exportConfig = new BehaviorSubject<TransactionsExportConfig>({});
  exportErrorMessagesRef: { [key: string]: TemplateRef<string> | undefined } = {};
  storedExportType: TransactionsExportTypeElement | undefined;
  numberOfResultsFound = new ReplaySubject<number>();
  numberOfResultsDisplayed = new ReplaySubject<number>();
  checkImagesList: Observable<TransactionCheckImageResponse> | undefined;
  transactionDetailsLoadingStatus: Observable<TransactionsDetailsLoading> | undefined;
  availableFiltersConfig: ReplaySubject<AvailableFiltersConfig> = new ReplaySubject<AvailableFiltersConfig>(1);
  availableFilters: Observable<AvailableFilters> = this.availableFiltersConfigService.availableFilters$;

  private readonly refreshTransactionsListHandler = () => {
    this.model.refreshTransactions();
  };

  private clearRefreshTransactionsListEvents() {
    this.registeredRefreshTransactionsListEvents.forEach((eventName) => {
      this.eventBusService.unsubscribe(eventName, this.refreshTransactionsListHandler);
    });

    this.registeredRefreshTransactionsListEvents = [];
  }

  retry() {
    this.model.retryFiltering();
    this.pendingTransactionsService.retryFiltering();
  }

  ngOnDestroy() {
    this.clearRefreshTransactionsListEvents();
    this.unsubscribeSubject.next();
    this.unsubscribeSubject.complete();
  }

  /**
   * @deprecated will be removed in 11.0.0
   */
  handleError = (errorResponse: HttpErrorResponse) => {
    this.errors = TransactionsBackendError.fromHttpErrorsResponse(errorResponse);

    return of({
      items: [],
      totalCount: 0,
    });
  };

  get pageSize(): Observable<number> {
    return filterInputParameters(this.pageSizeSubject.asObservable(), this.widgetProperties.pageSize);
  }

  get maxNavPages(): Observable<number> {
    return filterInputParameters(this.maxNavPagesSubject.asObservable(), this.widgetProperties.maxNavPages);
  }

  get showControls(): Observable<boolean> {
    return filterInputParameters(this.showControlsSubject.asObservable(), this.widgetProperties.showControls);
  }

  get isFilterApplied() {
    return this.model.isFilterApplied;
  }

  get isSearchApplied() {
    return this.model.isSearchApplied;
  }

  get paginationType(): Observable<PaginationType> {
    return filterInputParameters(this.paginationTypeSubject.asObservable(), this.widgetProperties.paginationType);
  }

  get showPendingTransactionsOnTop(): Observable<boolean> {
    return filterInputParameters(
      this.showPendingTransactionsOnTopSubject.asObservable(),
      this.widgetProperties.showPendingTransactionsOnTop,
    );
  }

  get showChangeCategory(): Observable<boolean> {
    return filterInputParameters(
      this.showChangeCategorySubject.asObservable(),
      this.widgetProperties.showChangeCategory,
    );
  }

  get showCheckImage(): Observable<boolean> {
    return filterInputParameters(this.showCheckImageSubject.asObservable(), this.widgetProperties.showCheckImage);
  }

  get showManageNotes(): Observable<boolean> {
    return filterInputParameters(this.showManageNotesSubject, this.widgetProperties.showManageNotes);
  }

  get getOrPostEndpoint(): Observable<EndpointType> {
    return filterInputParameters(
      this.getOrPostEndpointsSubject.asObservable(),
      this.widgetProperties.getOrPostEndpoints,
    );
  }

  get gmapsAPIKey(): Observable<string> {
    return combineLatest([this.apiKeySubject.asObservable(), this.widgetProperties.gmapsAPIKey]).pipe(
      map(([inputVal, propVal]) => inputVal || propVal),
    );
  }

  get enableDisputeAndInquiry() {
    return filterInputParameters(
      this.enableDisputeAndInquirySubject.asObservable(),
      this.widgetProperties.enableDisputeAndInquiry,
    );
  }

  get disputeByBillingStatus(): Observable<boolean> {
    return filterInputParameters(
      this.disputeByBillingStatusSubject.asObservable(),
      this.widgetProperties.disputeByBillingStatus,
    );
  }

  get disputeEligibilityDays(): Observable<number> {
    return filterInputParameters(
      this.disputeEligibilityDaysSubject.asObservable(),
      this.widgetProperties.disputeEligibilityDays,
    );
  }

  get disputeTransactionTypes(): Observable<Array<string>> {
    return filterInputParameters(
      this.disputeTransactionTypesSubject.asObservable(),
      this.widgetProperties.disputeTransactionTypes,
    );
  }

  private inquireTopicId = filterInputParameters(
    this.inquireTopicIdSubject.asObservable(),
    this.widgetProperties.inquireTopicId,
  );

  private disputeTopicId = filterInputParameters(
    this.disputeTopicIdSubject.asObservable(),
    this.widgetProperties.disputeTopicId,
  );

  inquireAndDisputeTopics: Observable<InquireAndDisputeTopics> = combineLatest([
    this.inquireTopicId,
    this.disputeTopicId,
  ]).pipe(map(([inquire, dispute]) => ({ inquire, dispute })));

  onLoadMore() {
    this.model.loadMore();
  }

  ngOnInit() {
    if (typeof this.selectedAccount === 'undefined') {
      throw new Error(`"selectedAccount" input is required in "${this.constructor.name}"`);
    }

    const initialListOptions: Observable<Partial<TransactionsListOptions>> = combineLatest([
      this.initialFilterOptions,
      this.initialSortOptions,
    ]).pipe(
      map(([initialFilterOptions, initialSortOptions]) => ({
        filter: initialFilterOptions,
        sort: initialSortOptions,
      })),
    );

    this.model.getTransactionsFrom(
      this.selectedAccount,
      this.pageSize,
      initialListOptions,
      this.showPendingTransactionsOnTop,
      this.getOrPostEndpoint,
    );
    if (this.showDetailsConfig) {
      this.checkImagesList = this.transactionDetailsService.fromCheckImages(this.showCheckImage);

      this.transactionDetailsLoadingStatus = this.transactionDetailsService.loadingState;

      this.transactionDetailsService
        .fromGeolocation(this.gmapsAPIKey)
        .pipe(takeUntil(this.unsubscribeSubject))
        .subscribe();
    }

    this.transactionsList = this.model.getTransactionsList().pipe(
      publishReplay(1),
      refCount(),
      tap((data) => {
        this.errors = data.httpResponseError
          ? TransactionsBackendError.fromHttpErrorsResponse(data.httpResponseError)
          : undefined;
        this.cd.markForCheck();
      }),
    );

    this.pendingTransactionsService.getTransactionsFrom(
      this.selectedAccount,
      initialListOptions,
      this.getOrPostEndpoint,
    );
    this.pendingTransactionsList = this.pendingTransactionsService.getTransactionsList().pipe(
      publishReplay(1),
      refCount(),
      tap((data) => {
        this.errors = data.httpResponseError
          ? TransactionsBackendError.fromHttpErrorsResponse(data.httpResponseError)
          : undefined;
        this.cd.markForCheck();
      }),
    );

    this.transactionsList
      .pipe(
        takeUntil(this.unsubscribeSubject),
        map((transactionsList) => {
          if (!transactionsList) {
            return {};
          }

          const { availableExportTypesHeader, unavailableExportTypesReasonsHeader } = transactionsList;

          return combineExportTypesConfig({
            availableExportTypesHeader,
            unavailableExportTypesReasonsHeader,
          });
        }),
      )
      .subscribe(this.exportConfig);

    const transactionsListCount = this.transactionsList.pipe(
      map((trans) => ({ history: trans ? trans.totalCount : 0 })),
    );

    const pendingTransactionsListCount = this.pendingTransactionsList.pipe(
      map((trans) => ({ pending: trans ? trans.totalCount : 0 })),
    );

    const transactionsListItemsToDisplay = this.transactionsList.pipe(
      map((trans) => ({ history: trans ? trans.items.length : 0 })),
    );

    const pendingTransactionsListItemsToDisplay = this.pendingTransactionsList.pipe(
      map((trans) => ({ pending: trans ? trans.items.length : 0 })),
    );

    this.sumTransactionsResults(transactionsListCount, pendingTransactionsListCount)
      .pipe(takeUntil(this.unsubscribeSubject))
      .subscribe(this.numberOfResultsFound);

    this.sumTransactionsResults(transactionsListItemsToDisplay, pendingTransactionsListItemsToDisplay)
      .pipe(takeUntil(this.unsubscribeSubject))
      .subscribe(this.numberOfResultsDisplayed);

    this.currentPage = this.model.getCurrentPage();
    this.loadingState = this.model.loadingState;
    this.loadingPendingState = this.pendingTransactionsService.loadingState;

    this.widgetProperties.refreshTransactionsListEvents.pipe(takeUntil(this.unsubscribeSubject)).subscribe((events) => {
      this.clearRefreshTransactionsListEvents();
      if (events) {
        this.registeredRefreshTransactionsListEvents = events.split(',');
        this.registeredRefreshTransactionsListEvents.forEach((eventName) => {
          this.eventBusService.subscribe(eventName, this.refreshTransactionsListHandler);
        });
      }
    });

    this.configureTransctionsDetailsOutputBehaviour();

    this.transactionDetailsService.fromInquiryAndDispute(
      this.enableDisputeAndInquiry,
      this.disputeByBillingStatus,
      this.disputeEligibilityDays,
      this.disputeTransactionTypes,
    );

    this.availableFiltersConfigService.setFilterConfigurationAndAccount(
      this.availableFiltersConfig.asObservable(),
      this.selectedAccount,
    );
  }

  private configureTransctionsDetailsOutputBehaviour() {
    if (this.transactionsList && this.pendingTransactionsList && !this.showDetailsConfig) {
      const pending = this.showPendingTransactionsOnTop.pipe(
        mergeMap((cond) =>
          iif(() => cond, this.pendingTransactionsList, of({ items: [], totalCount: 0 } as TransactionsList)),
        ),
      );

      combineLatest([pending, this.transactionsList])
        .pipe(
          takeUntil(this.unsubscribeSubject),
          map(([pendingTransactionsList, transactionsList]) => {
            const pendingTransactions = pendingTransactionsList ? pendingTransactionsList.items : [];
            const transactions = transactionsList ? transactionsList.items : [];

            const mixed: TransactionsList = {
              items: [...pendingTransactions, ...transactions],
              totalCount: (pendingTransactionsList?.totalCount || 0) + (transactionsList?.totalCount || 0),
            };

            return mixed;
          }),
        )
        .subscribe((data) => {
          this.transactionsLoaded.emit(data);
        });
    }
  }

  private sumTransactionsResults(historyCount: Observable<any>, pendingCount: Observable<any>) {
    return this.showPendingTransactionsOnTop.pipe(
      mergeMap((showPendingTransactionsOnTop) =>
        iif(() => showPendingTransactionsOnTop, merge(historyCount, pendingCount), historyCount),
      ),
      scan((acc, curr) => ({ ...acc, ...curr }), { history: 0, pending: 0 }),
      map(({ history = 0, pending = 0 }) => history + pending),
    );
  }

  onPageChange(page: number) {
    this.model.pageChange(page);
  }

  onSearch(query: string) {
    // search ui component sends an unexpected event
    // that is type Event. To avoid calling backend with that event
    // it has to be string
    if (typeof query === 'string') {
      this.model.search(query);
      this.pendingTransactionsService.search(query);
    }
  }

  onFilter(filterOptions: TransactionsFilterOptions) {
    this.model.filter(filterOptions);
    this.pendingTransactionsService.filter(filterOptions);
  }

  onSort(sortOptions: TransactionsSortOptions) {
    this.model.sort(sortOptions);
  }

  onPrint() {
    window.print();
  }

  onExport(exportType: TransactionsExportTypeElement) {
    this.storedExportType = { ...exportType };
    if (exportType.isAvailable) {
      this.model
        .exportToType(exportType.name)
        .pipe(takeUntil(this.unsubscribeSubject))
        .subscribe(
          (response) => {
            const anchorTag = document.createElement('a');
            anchorTag.href = URL.createObjectURL(response.body);

            const fileName = getNameFromContentDispositionHeader(response);
            const defaultName = exportType.name.toLowerCase() === 'pdf' ? 'Account Activities' : 'transactions';

            anchorTag.download = fileName !== '' ? fileName : defaultName;
            anchorTag.click();
          },
          () => {
            this.notificationService.showNotification({
              header: this.notificationServerErrorHeader,
              message: this.notificationServerErrorMessage || '',
              modifier: 'error',
            });
          },
        );
    } else {
      this.pushNotification({
        header: this.notificationErrorUpdateHeaderTemplate,
        message: this.notificationErrorUpdateMessageTemplate,
        modifier: 'error',
      });
    }
  }

  onDetailOpen(transaction: Transaction) {
    if (!this.showDetailsConfig) {
      this.transactionSelected.emit(transaction);
    } else {
      this.transactionDetailsService.selectTransaction(transaction);
    }
  }

  private pushNotification(notificationMessage: {
    header: TemplateRef<any> | string | undefined;
    message: any;
    modifier: 'error' | 'success' | 'info' | 'warning' | 'alert' | undefined;
  }) {
    this.notificationService.showNotification({
      dismissible: true,
      ttl: notificationTtl,
      ...notificationMessage,
    });
  }

  constructor(
    protected readonly model: TransactionsService,
    protected readonly widgetProperties: WidgetPropertiesService,
    protected readonly cd: ChangeDetectorRef,
    protected readonly eventBusService: Pubsub,
    protected readonly notificationService: NotificationService,
    protected readonly pendingTransactionsService: PendingTransactionsService,
    protected readonly transactionDetailsService: TransactionDetailsService,
    protected readonly availableFiltersConfigService: AvailableFiltersConfigService,
    protected readonly showDetailsConfig?: boolean,
  ) {}
}

@Directive({
  selector: '[bbTransactionsNoAccountSelectedStateCustomizable]',
})
export class TransactionsNoAccountSelectedStateCustomizableDirective extends BbTemplate<
  BaseTransactionsContainer,
  undefined
> {}

@Directive({
  selector: '[bbTransactionsErrorStateCustomizable]',
})
export class TransactionsErrorStateCustomizableDirective extends BbTemplate<BaseTransactionsContainer, undefined> {}
