import { Injectable } from '@angular/core';
import { MessagecenterHttpService } from '@backbase/data-ang/messages';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { b64Encode } from '../constants';
import { Message, MessagesResponse } from '../model/messages.model';
import { Transaction } from '../model/transaction.model';

@Injectable()
export class MessagesService {
  constructor(private readonly messageCenterHttpService: MessagecenterHttpService) {}

  saveMessages(message: Message): Observable<MessagesResponse> {
    return this.messageCenterHttpService.postMessages({ presentationMessagePostRequestBody: message });
  }

  saveAttachment(attachment: Blob): Observable<string> {
    return this.messageCenterHttpService.postMessageAttachments({ attachment }).pipe(map((data) => data.id));
  }

  deleteAttachments(attachmentIds: Array<string>): Observable<Array<string>> {
    return this.messageCenterHttpService
      .deleteMessageAttachments({ ids: attachmentIds })
      .pipe(map((data) => data.deleted));
  }

  createDefaultBody(transaction: Transaction) {
    return `transactionID: ${transaction.id}
referenceNumber: ${transaction.reference}
arranagementID: ${transaction.arrangementId}
bookingDate: ${transaction.bookingDate}
amount: ${transaction.transactionAmountCurrency?.amount}
merchantName: ${transaction.merchant?.name}
counterpartyName: ${transaction.counterPartyName}
description: ${transaction.description}
type: ${transaction.type}`;
  }

  // TODO: default message is out of scope
  defaultMessage: Observable<Message> = of({
    body: b64Encode('test message'),
    subject: 'no subject',
    topic: 'no topic',
    sender: {
      name: 'test sender',
    },
  });
}
