import { HttpErrorResponse } from '@angular/common/http';

/**
 * This interface is used by the template to display what is wrong to the user.
 */
export interface TransactionsBackendError {
  message: string;
  key?: string;
  context?: { [key: string]: string };
}

/* eslint-disable no-redeclare*/
export namespace TransactionsBackendError {
  const getMessageFromResponse = (error: HttpErrorResponse): string[] | undefined => {
    const responseBody = error.error;
    if (Array.isArray(responseBody?.errors)) {
      return responseBody.errors.map((data: TransactionsBackendError) => {
        const { context = {} } = data;
        const message = data.message.replace(/{[_a-zA-Z][_a-zA-Z0-9]*}/g, (match) => {
          const result = context[match.replace(/[\{\}']+/g, '')];

          return result !== undefined ? result : match;
        });

        return `Error ${error.status}: ${message}`;
      });
    } else {
      return undefined;
    }
  };
  /* eslint-enable no-redeclare*/
  /**
   * @name fromHttpErrorResponse
   * @param errorResponse The error response
   * @returns An object containing a user-friendly error message
   *
   * @description
   * Parse an HttpErrorResponse and return a TransactionsError object that can be used to display a
   * user-friendly error message.
   */
  export const fromHttpErrorResponse = (errorResponse: HttpErrorResponse): TransactionsBackendError => {
    if (errorResponse.error instanceof ErrorEvent) {
      // Client-side or network error.
      return {
        message: 'An unexpected error occurred',
      };
    } else {
      const error = getMessageFromResponse(errorResponse) || [];

      return {
        message: error[0] || `Error ${errorResponse.status}`,
      };
    }
  };

  export const fromHttpErrorsResponse = (errorResponse: HttpErrorResponse): TransactionsBackendError[] => {
    if (errorResponse.error instanceof ErrorEvent || !errorResponse.status) {
      // Client-side or network error.
      return [
        {
          message: 'An unexpected error occurred',
        },
      ];
    } else {
      const errors = getMessageFromResponse(errorResponse) || [`Error ${errorResponse.status}`];

      return errors.map((message) => ({ message }));
    }
  };
}
