import { Inject, Injectable, InjectionToken, LOCALE_ID, OnDestroy } from '@angular/core';
import {
  GetTransactionsExportWithPostRequestParams,
  GetTransactionsWithPostRequestParams,
  TransactionClientHttpService,
  TransactionItem,
} from '@backbase/data-ang/transactions';
import { BehaviorSubject, combineLatest, iif, merge, Observable, of, ReplaySubject, Subject } from 'rxjs';
import { catchError, distinctUntilChanged, filter, first, map, scan, switchMap, tap } from 'rxjs/operators';
import {
  defaultFilterOptions,
  defaultInitialExportOptions,
  defaultInitialListOptions,
  defaultPaginationOptions,
  defaultSearchOptions,
  OnLoadAction,
  TransactionsListOptions,
  TransactionState,
} from '../model/transactions-list-options.model';
import { TransactionsExportOptions } from '../model/transactions-export-options.model';
import { TransactionsSearchOptions } from '../model/transactions-search-options.model';
import { TransactionsFilterOptions } from '../model/transactions-filter-options.model';
import { TransactionsList } from '../model/transactions-list.model';
import { TransactionsSortOptions } from '../model/transactions-sort-options.model';
import { AccountsService } from './accounts.service';
import { Account } from '../model/account.model';
import {
  cacheRequest,
  deepEqual,
  multipleAccountsPredicate,
  onLoadTransactions,
} from '../model/transactions-list-utils.model';
import { HttpResponse } from '@angular/common/http';
import { EndpointType } from './widget-properties.service';
import { DeprecationsService } from '@backbase/foundation-ang/core';

export enum LoadingState {
  NotLoaded = 'NotLoaded',
  LoadingMore = 'LoadingMore',
  LoadingPage = 'LoadingPage',
  Loading = 'Loading',
  Loaded = 'Loaded',
}

export const showDetailsConfigToken = new InjectionToken<boolean>('shows the transactions details modal');

@Injectable()
export class TransactionsService implements OnDestroy {
  private readonly selectedAccount = new ReplaySubject<string[]>(1);
  private readonly pageSize = new ReplaySubject<number>(1);
  private readonly initialListOptions = new ReplaySubject<Partial<TransactionsListOptions>>(1);
  private readonly options = new BehaviorSubject<Partial<TransactionsListOptions>>({});
  private readonly listRefresh = new Subject<TransactionsListOptions>();
  private readonly listOptions = new BehaviorSubject<TransactionsListOptions>(defaultInitialListOptions);
  private readonly exportOptions = new BehaviorSubject<TransactionsExportOptions>(defaultInitialExportOptions);
  private readonly showPendingTransactionsOnTop = new ReplaySubject<boolean>(1);
  private readonly getOrPostEndpoint = new BehaviorSubject<EndpointType>(EndpointType.GET_REQUEST);
  private readonly transactionsList: Observable<TransactionsList> = merge(
    this.listOptions.pipe(
      filter((listOptions) => typeof listOptions.account.arrangementId !== 'undefined'),
      distinctUntilChanged(deepEqual),
    ),
    this.listRefresh,
  ).pipe(
    cacheRequest((listOptions) => {
      this.loadingState.next(
        listOptions.onLoad === OnLoadAction.Append
          ? LoadingState.LoadingMore
          : listOptions.onLoad === OnLoadAction.ReplacePage
          ? LoadingState.LoadingPage
          : LoadingState.Loading,
      );

      return combineLatest([
        this.loadTransactions(listOptions).pipe(
          tap(() => {
            this.loadingState.next(LoadingState.Loaded);
          }),
          map((transactionsList) => ({ transactionsList, onLoad: listOptions.onLoad })),
        ),
        iif(
          multipleAccountsPredicate(listOptions),
          this.accountService.getAllArrangements().pipe(
            map((accounts) => {
              const accountMap = new Map<string, Account>();
              accounts.forEach((account) => {
                accountMap.set(account.id, account);
              });

              return accountMap;
            }),
          ),
          of(new Map()),
        ),
        this.isFilterApplied,
        this.isSearchApplied,
      ]).pipe(
        map(([transactions, accountMap, isFilterApplied, isSearchApplied]) => {
          transactions.transactionsList.items = transactions.transactionsList.items.map((transaction) => ({
            ...transaction,
            account: accountMap.get(transaction.arrangementId || ''),
          }));
          transactions.transactionsList.initialRequest = !(isFilterApplied || isSearchApplied);

          return transactions;
        }),
      );
    }),
    scan(onLoadTransactions, { items: [], totalCount: 0 }),
  );

  public readonly loadingState = new BehaviorSubject<LoadingState>(LoadingState.NotLoaded);

  public readonly isFilterApplied = this.listOptions.pipe(
    map((options) => !TransactionsFilterOptions.isEmpty(options.filter || {})),
  );

  public readonly isSearchApplied = this.listOptions.pipe(
    map((options) => !TransactionsFilterOptions.isEmpty(options.search || {})),
  );

  private loadTransactions(listOptions: TransactionsListOptions): Observable<TransactionsList> {
    const transactionPayload = TransactionsList.toHttpRequest(listOptions);
    let transactionsResponse: Observable<HttpResponse<Array<TransactionItem>>> | undefined;

    if (this.isUsingPostEndpoints) {
      transactionsResponse = this.transactionsDataHttpService.getTransactionsWithPost(
        { transactionListRequest: transactionPayload } as GetTransactionsWithPostRequestParams,
        'response',
      );
    } else {
      this.deprecationsService.logDeprecatedFeature(
        '[TransactionsService: loadTransactions] GET endpoints deprecated. Please use POST endpoints by changing CXP configuration',
      );
      transactionsResponse = this.transactionsDataHttpService.getTransactions(transactionPayload, 'response');
    }

    return transactionsResponse.pipe(
      map(TransactionsList.fromHttpResponse),
      catchError((err) =>
        of({
          items: [],
          totalCount: 0,
          httpResponseError: err,
        }),
      ),
    );
  }

  private exportTransactions(exportOptions: TransactionsExportOptions): Observable<HttpResponse<Blob>> {
    const exportPayload = TransactionsList.toHttpRequest({
      ...exportOptions,
      locale: this.locale,
    });

    if (this.isUsingPostEndpoints) {
      return this.transactionsDataHttpService.getTransactionsExportWithPost(
        { transactionListRequest: exportPayload } as GetTransactionsExportWithPostRequestParams,
        'response',
      );
    } else {
      this.deprecationsService.logDeprecatedFeature(
        '[TransactionsService: exportTransactions] GET endpoints deprecated. Please use POST endpoints by changing CXP configuration',
      );

      return this.transactionsDataHttpService.getTransactionsExport(exportPayload, 'response');
    }
  }

  search(query: string) {
    const searchOptions: TransactionsSearchOptions = {
      query,
    };

    const paginationOptions = {
      ...(this.options.getValue().pagination || defaultPaginationOptions.pagination),
      from: 0, // reset to 0
    };

    this.options.next({
      ...this.options.getValue(),
      ...defaultSearchOptions,
      pagination: paginationOptions,
      search: searchOptions,
      onLoad: OnLoadAction.Replace,
    });
  }

  clearSearch() {
    const paginationOptions = {
      ...(this.options.getValue().pagination || defaultPaginationOptions.pagination),
      from: 0, // reset to 0
    };

    this.options.next({
      ...this.options.getValue(),
      ...defaultSearchOptions,
      pagination: paginationOptions,
      onLoad: OnLoadAction.Replace,
    });
  }

  filter(filterOptions: TransactionsFilterOptions) {
    const paginationOptions = {
      ...(this.options.getValue().pagination || defaultPaginationOptions.pagination),
      from: 0, // reset to 0
    };

    this.options.next({
      ...this.options.getValue(),
      ...defaultFilterOptions,
      pagination: paginationOptions,
      filter: { ...filterOptions },
      onLoad: OnLoadAction.Replace,
    });
  }

  clearFilter() {
    const paginationOptions = {
      ...(this.options.getValue().pagination || defaultPaginationOptions.pagination),
      from: 0, // reset to 0
    };

    this.options.next({
      ...this.options.getValue(),
      ...defaultFilterOptions,
      pagination: paginationOptions,
      onLoad: OnLoadAction.Replace,
    });
  }

  exportToType(type: string) {
    return this.exportOptions.pipe(
      first(),
      switchMap((exportOptions: TransactionsExportOptions) =>
        this.exportTransactions({
          ...exportOptions,
          exportType: type,
        }),
      ),
    );
  }

  loadMore() {
    const currentPaginationOptions = this.options.getValue().pagination || defaultPaginationOptions.pagination;
    const paginationOptions = {
      ...currentPaginationOptions,
      from: currentPaginationOptions.from + 1,
    };

    this.options.next({
      pagination: paginationOptions,
      onLoad: OnLoadAction.Append,
    });
  }

  sort(sortOptions: TransactionsSortOptions) {
    const currentPaginationOptions = this.options.getValue().pagination || defaultPaginationOptions.pagination;
    this.options.next({
      sort: { ...sortOptions },
      pagination: {
        ...currentPaginationOptions,
        from: 0,
      },
      onLoad: OnLoadAction.ReplacePage,
    });
  }

  pageChange(page: number) {
    const paginationOptions = {
      ...defaultPaginationOptions.pagination,
      ...this.options.getValue().pagination,
      from: page,
    };

    this.options.next({
      pagination: paginationOptions,
      onLoad: OnLoadAction.ReplacePage,
    });
  }

  getTransactionsFrom(
    selectedAccount: Observable<string[]>,
    pageSize: Observable<number>,
    initialListOptions: Observable<Partial<TransactionsListOptions>> = of({}),
    showPendingTransactionsOnTop: Observable<boolean> = of(false),
    getOrPostEndpoint: Observable<EndpointType> = of(EndpointType.GET_REQUEST),
  ) {
    selectedAccount.subscribe(this.selectedAccount);
    pageSize.subscribe(this.pageSize);
    initialListOptions.subscribe(this.initialListOptions);
    showPendingTransactionsOnTop.subscribe(this.showPendingTransactionsOnTop);
    getOrPostEndpoint.subscribe(this.getOrPostEndpoint);
  }

  getCurrentPage() {
    return this.listOptions.pipe(map((options) => (options.pagination || defaultPaginationOptions.pagination).from));
  }

  getTransactionsList() {
    return this.transactionsList;
  }

  retryFiltering() {
    this.listRefresh.next(this.listOptions.getValue());
  }

  refreshTransactions() {
    const currentPaginationOptions = this.options.getValue().pagination || defaultPaginationOptions.pagination;
    const paginationOptions = {
      ...currentPaginationOptions,
      from: 0,
    };

    const newOptions = {
      ...this.listOptions.getValue(),
      pagination: paginationOptions,
      onLoad: OnLoadAction.ReplacePage,
    };

    if (deepEqual(newOptions, this.listOptions.getValue())) {
      this.listRefresh.next(this.listOptions.getValue());
    } else {
      this.options.next({
        pagination: paginationOptions,
        onLoad: OnLoadAction.ReplacePage,
      });
    }
  }

  private get isUsingPostEndpoints() {
    return this.getOrPostEndpoint.getValue() === EndpointType.POST_REQUEST;
  }

  ngOnDestroy() {
    this.listRefresh.complete();
    this.selectedAccount.complete();
    this.pageSize.complete();
    this.initialListOptions.complete();
    this.options.complete();
    this.listOptions.complete();
    this.exportOptions.complete();
    this.showPendingTransactionsOnTop.complete();
    this.loadingState.complete();
    this.getOrPostEndpoint.complete();
  }

  constructor(
    private readonly transactionsDataHttpService: TransactionClientHttpService,
    private readonly accountService: AccountsService,
    private readonly deprecationsService: DeprecationsService,
    @Inject(LOCALE_ID) private readonly locale: string,
  ) {
    // Base this.options on this.selectedAccount, this.pageSize, this.initialListOptions
    combineLatest([this.selectedAccount, this.pageSize, this.initialListOptions, this.showPendingTransactionsOnTop])
      .pipe(
        map(([selectedAccount, pageSize, initialListOptions, showPendingTransactionsOnTop]) => ({
          ...(showPendingTransactionsOnTop ? { state: TransactionState.completed } : {}),
          account: {
            arrangementId: selectedAccount,
          },
          pagination: {
            from: 0,
            size: pageSize,
          },
          onLoad: OnLoadAction.Replace,
          ...initialListOptions,
        })),
      )
      .subscribe(this.options);

    // Base this.listOptions on this.options
    this.options
      .pipe(scan((acc: any, curr) => Object.assign({}, acc, curr), defaultInitialListOptions))
      .subscribe(this.listOptions);

    // Base this.exportOptions on this.listOptions
    this.listOptions
      .pipe(
        filter((listOptions) => typeof listOptions.account.arrangementId !== 'undefined'),
        map((params) => ({
          ...params,
          pagination: undefined,
          state: undefined,
          exportType: 'csv',
          locale: 'en-US',
        })),
      )
      .subscribe(this.exportOptions);
  }
}
