import { HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { switchMap, publishReplay, refCount } from 'rxjs/operators';
import { TransactionsListOptions, OnLoadAction } from './transactions-list-options.model';
import { TransactionsList } from './transactions-list.model';

export const deepEqual = (x: any, y: any): boolean => {
  const ok = Object.keys;
  const tx = typeof x;
  const ty = typeof y;

  return x && y && tx === 'object' && tx === ty
    ? ok(x).length === ok(y).length && ok(x).every((key) => deepEqual(x[key], y[key]))
    : x === y;
};

export const cacheRequest =
  <Params, Result>(request: (params: Params) => Observable<Result>) =>
  (source: Observable<Params>): Observable<Result> =>
    source.pipe(switchMap(request), publishReplay(1), refCount());

export const multipleAccountsPredicate = (listOptions: TransactionsListOptions) => () =>
  Array.isArray(listOptions.account.arrangementId);

export const onLoadTransactions = (
  acc: TransactionsList,
  curr: { transactionsList: TransactionsList; onLoad: OnLoadAction },
) =>
  curr.onLoad === OnLoadAction.Append ? TransactionsList.concat(acc, curr.transactionsList) : curr.transactionsList;

export const getNameFromContentDispositionHeader = (response: HttpResponse<Blob>) => {
  const contentDisposition = response.headers.get('Content-Disposition');
  let fileName = '';
  if (contentDisposition) {
    const result = /filename[^;=\n]*=(?:(\\?['"])(.*?)\1|(?:[^\s]+'.*?')?([^;\n]*))/i.exec(contentDisposition);
    fileName = result ? result[2] : '';
  }

  return fileName;
};
