import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ElementRef,
  HostListener,
  ViewContainerRef,
  ViewChild,
  OnDestroy,
} from '@angular/core';
import {
  DocumentRefSrc,
  DocumentViewerOptions,
  DocumentViewerType,
  DOCUMENT_LOAD_DELAY_MS,
  NavigationState,
  Styles,
} from '../../../model/UI/document-viewer-commons';
import { Observable, ReplaySubject, combineLatest, timer, BehaviorSubject, Subject } from 'rxjs';
import { catchError, distinctUntilChanged, finalize, map, share, switchMap, tap } from 'rxjs/operators';

@Component({
  selector: 'bb-document-viewer-container',
  templateUrl: './document-viewer-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DocumentViewerContainerComponent implements OnInit, OnDestroy {
  @Input() documentRefSrcs: DocumentRefSrc[] = [];
  @Input() set activeDocumentRefIdx(idx: number) {
    this.activeDocumentRefIdx$.next(idx);
  }
  @Input() options?: DocumentViewerOptions;
  @Output() close = new EventEmitter<void>();
  // eslint-disable-next-line
  @ViewChild('viewerContainer', { static: true, read: ViewContainerRef }) readonly viewContainer!: ViewContainerRef;
  documentSrc$!: Observable<string>;
  documentFilename$!: Observable<string>;
  documentFiletype$!: Observable<DocumentViewerType>;
  navigationState$!: Observable<NavigationState>;
  readonly isLoading$ = new BehaviorSubject(false);
  readonly isHttpError$ = new BehaviorSubject(false);
  readonly documentType = DocumentViewerType;
  /**
   * Internal
   */
  private readonly activeDocumentRefIdx$ = new ReplaySubject<number>(1);
  private readonly destroy$ = new Subject<void>();

  constructor(private readonly elemRef: ElementRef) {}

  get containerStyles(): Styles | undefined {
    return this.options?.container?.styles;
  }

  get viewerScale(): number | undefined {
    return this.options?.scale;
  }

  ngOnInit() {
    this.setDocumentRefChangeSources();
    this.setFocus();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  @HostListener('document:keydown.escape') onEscape() {
    this.close.emit();
  }

  closeDialog() {
    this.close.emit();
  }

  onNavigate(idx: number): void {
    this.activeDocumentRefIdx$.next(idx);
  }

  private setDocumentRefChangeSources(): void {
    this.documentSrc$ = this.activeDocumentRefIdx$.pipe(
      distinctUntilChanged(),
      tap(() => {
        this.isLoading$.next(true);
        this.isHttpError$.next(false);
      }),
      switchMap((idx) =>
        combineLatest([this.documentRefSrcs[idx].src, timer(DOCUMENT_LOAD_DELAY_MS)]).pipe(
          map(([src]) => src),
          finalize(() => {
            this.isLoading$.next(false);
          }),
        ),
      ),
      catchError((error) => {
        this.isHttpError$.next(true);

        throw error;
      }),
      share(),
    );
    this.documentFilename$ = this.activeDocumentRefIdx$.pipe(map((idx) => this.documentRefSrcs[idx].filename));
    this.documentFiletype$ = this.activeDocumentRefIdx$.pipe(map((idx) => this.documentRefSrcs[idx].filetype));
    this.navigationState$ = this.activeDocumentRefIdx$.pipe(
      map((idx) => ({ currIdx: idx, total: this.documentRefSrcs.length })),
    );
  }

  private setFocus(): void {
    this.elemRef.nativeElement.focus();
  }
}
