export type ProductKindName =
  | 'Current Account'
  | 'Savings Account'
  | 'Credit Card'
  | 'Loan'
  | 'Term Deposit'
  | 'Debit Card'
  | 'Investment Account'
  | string;

export type AvailableFiltersConfig = Record<ProductKindName, ProductKind>;

export enum AvailableFilterEnum {
  CreditDebitIndicator = 'creditDebitIndicator',
  Types = 'types',
  BillingStatus = 'billingStatus',
  Amount = 'amount',
  Date = 'date',
  Categories = 'categories',
  CheckSerialNumber = 'checkSerialNumber',
}

export type AvailableFilters = Array<AvailableFilterEnum>;

export type ProductKind = ProductType | AvailableFilters;

export interface ProductType {
  [key: string]: AvailableFilters;
}
