export const deserialize = (propertyValue: string) => {
  const params = propertyValue.split('&');

  return params.reduce((acc: object, param: string) => {
    const [key, val] = param.split('=');

    return {
      ...acc,
      [decodeURIComponent(key)]: decodeURIComponent(val),
    };
  }, {});
};

export const parseSortOptions = (propertyValue: string): TransactionsSortOptions | undefined => {
  if (!propertyValue.length) {
    return undefined;
  }

  const sortOption = propertyValue
    .split(',')[0]
    .trim()
    .replace(/\s{2,}/g, ' ');

  const [orderBy, direction] = sortOption.split(' ');

  if (!orderBy || (direction !== 'ASC' && direction !== 'DESC')) {
    throw new Error(
      `Invalid value for "initialSortOptions" property.
      Please use "<orderBy> <direction>" format, e.g., "bookingDate DESC", or leave it empty.`,
    );
  }

  return {
    orderBy,
    direction,
  };
};

export const filterInputParameters = <T>(input: Observable<T | undefined>, prop: Observable<T>): Observable<T> =>
  combineLatest([input, prop]).pipe(map(([inputVal, propVal]) => inputVal ?? propVal));

export const b64Encode = (str: string) =>
  btoa(
    encodeURIComponent(str).replace(/%([0-9A-F]{2})/g, function (_match, p1) {
      return String.fromCharCode(parseInt(p1, 16));
    }),
  );

export const truncateDate = (date: Date) => new Date(date.getFullYear(), date.getMonth(), date.getDate());

export const daysDiff = (date1: Date | string, date2: Date | string) => {
  const diff = truncateDate(new Date(date1)).getTime() - truncateDate(new Date(date2)).getTime();

  return Math.ceil(diff / (1000 * 3600 * 24));
};
