import { HttpResponse } from '@angular/common/http';
import { TransactionCheckImagesResponse, TransactionCheckImagesItem } from '@backbase/data-ang/transactions';

export enum TransactionsCheckImageType {
  PNG = 'image/png',
  JPEG = 'image/jpeg',
}

export enum TransactionsCheckImageSide {
  FRONT = 'FRONT',
  BACK = 'BACK',
}

export interface TransactionsCheckImage {
  side: TransactionsCheckImageSide;
  mimeType: TransactionsCheckImageType;
  content: string;
}

export interface TransactionCheckImageResponse {
  images: TransactionsCheckImage[];
}

export namespace TransactionsCheckImages {
  function processImages(imageObject: TransactionCheckImagesItem): TransactionsCheckImage {
    return {
      side: imageObject.side as TransactionsCheckImageSide,
      mimeType: imageObject.mimeType as TransactionsCheckImageType,
      content: imageObject.content,
    };
  }

  export function fromHttpResponse(
    response: HttpResponse<TransactionCheckImagesResponse>,
  ): TransactionCheckImageResponse {
    const imagesArray = (response.body && response.body.images) || [];
    const mappedImages = imagesArray.map(processImages);

    return {
      images: mappedImages,
    };
  }
}
