import { Injectable } from '@angular/core';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { DisputeStatus, TransactionClientHttpService } from '@backbase/data-ang/transactions';
import { throwError, Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { Transaction } from '../model/transaction.model';

@Injectable()
export class TransactionUpdateService {
  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred.  Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong.
      console.error(`Backend returned code ${error.status}, ` + `body was: ${error.error}`);
    }

    // Return an observable with a user-facing error message.
    return throwError('Something bad happened; please try again later.');
  }

  update(transaction: Transaction, update: Partial<Transaction>): Observable<HttpResponse<any>> {
    if (typeof transaction.id === 'undefined' || typeof transaction.arrangementId === 'undefined') {
      return throwError('updateNotes: invalid transaction');
    }

    return this.transactionsDataHttpService
      .patchTransactions(
        {
          transactionsClientPatchRequestBody: [
            {
              id: transaction.id,
              arrangementId: transaction.arrangementId,
              notes: update.notes,
              disputeStatus: update.disputeStatus,
              category: update.category,
            },
          ],
        },
        'response',
      )
      .pipe(catchError(this.handleError));
  }

  setDisputeStatus(transaction: Transaction, disputeStatus: DisputeStatus) {
    return this.update(transaction, { disputeStatus });
  }

  updateNotes(transaction: Transaction, notes: string) {
    return this.update(transaction, { notes });
  }

  constructor(private readonly transactionsDataHttpService: TransactionClientHttpService) {}
}
