import { ChangeDetectionStrategy, Component, Input, Output, EventEmitter, NgZone, OnDestroy } from '@angular/core';
import { DocumentViewerType, downloadFile } from '../../../model/UI/document-viewer-commons';

@Component({
  selector: 'bb-document-viewer-header',
  templateUrl: './document-viewer-header.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DocumentViewerHeaderComponent implements OnDestroy {
  @Input() filename!: string;
  @Input() filetype?: DocumentViewerType;
  @Input() set source(src: string | undefined) {
    if (src) {
      this.revokeObjectURL();
      this.documentObjectURL = src;
    }
  }
  @Input() isLoading = false;
  @Output() close = new EventEmitter<void>();
  private documentObjectURL?: string;

  constructor(private readonly ngZone: NgZone) {}

  ngOnDestroy() {
    this.revokeObjectURL();
  }

  onOpenDocument(): void {
    if (!this.documentObjectURL) {
      return;
    }

    this.openImage(this.documentObjectURL);
  }

  onPrintDocument(): void {
    if (!this.documentObjectURL) {
      return;
    }

    this.printWindow(this.openImage(this.documentObjectURL));
  }

  onDownloadDocument(): void {
    if (!this.documentObjectURL) {
      return;
    }

    downloadFile(this.documentObjectURL, this.filename);
  }

  private openImage(documentObjectURL: string): Window {
    const image = new Image();
    const newWindow = window.open('about:blank', '_new') as Window;
    image.src = documentObjectURL;
    newWindow.document.write(image.outerHTML);

    return newWindow;
  }

  private printWindow(window: Window): void {
    this.ngZone.runOutsideAngular(() => {
      setTimeout(() => {
        window.focus();
        window.print();
      }, 0);
    });
  }

  private revokeObjectURL(): void {
    if (this.documentObjectURL) {
      window.URL.revokeObjectURL(this.documentObjectURL);
    }
  }
}
