import { HttpResponse } from '@angular/common/http';
import { EnumValuesByAttributeNameGetResponseBody } from '@backbase/data-ang/transactions';

export interface TransactionsEnumValue {
  value?: string;
  name?: string;
}
/* eslint-disable no-redeclare*/
export namespace TransactionsEnumValue {
  function toTitleCase(text: string | undefined) {
    if (!text) {
      return '';
    }

    return `${text.charAt(0).toUpperCase()}${text.substr(1).toLowerCase()}`;
  }

  // Until Angular supports i18n in typescript, this is the way to go
  function processEnumValue(enumValue: TransactionsEnumValue) {
    switch (enumValue.value) {
      case 'CRDT':
        return {
          ...enumValue,
          name: 'Credit',
        };
      case 'DBIT':
        return {
          ...enumValue,
          name: 'Debit',
        };
      case 'BILLED':
      case 'UNBILLED':
      case 'PENDING':
        return {
          ...enumValue,
          name: enumValue.name || toTitleCase(enumValue.value),
        };
      default:
        return {
          ...enumValue,
          name: enumValue.name || enumValue.value,
        };
    }
  }

  function processEnumValues(enumValues: Array<TransactionsEnumValue>) {
    return enumValues.map(processEnumValue);
  }

  export function fromHttpResponse(
    response: HttpResponse<EnumValuesByAttributeNameGetResponseBody[]>,
  ): Array<TransactionsEnumValue> {
    return processEnumValues((response.body as Array<{ value: string }>) || []);
  }
}
/* eslint-enable no-redeclare*/
