import {
  Component,
  OnInit,
  Input,
  ElementRef,
  ViewChild,
  AfterViewInit,
  ChangeDetectorRef,
  ChangeDetectionStrategy,
} from '@angular/core';
import { Transaction } from '../../model/transaction.model';
import { BaseTransactionIcon } from '../../model/transaction-category.model';
import { TransactionsIconCategoriesFactoryService } from '../../services/transactions-icon-categories-factory.service';

@Component({
  selector: 'bb-transaction-category-icon',
  templateUrl: './transaction-category-icon.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionCategoryIconComponent implements OnInit, AfterViewInit {
  /**
   * The transaction this component shows the category icon of.
   */
  @Input() transaction: Transaction | undefined;
  /**
   * Whether to show the icon in a circle.
   */
  @Input() circle = false;
  /**
   * Whether to show different sizes of icons.
   */
  @Input() size: 'small' | 'large' | undefined;
  /* eslint-disable */
  @ViewChild('categoryImage') categoryImage: ElementRef | undefined;
  /* eslint-enable */

  /**
   * Determines if the image is done loading.
   */
  hasImageLoaded = false;

  /**
   * Contains the transaction class name that has to be used together
   * with the title of the icon
   */
  transactionIcon: BaseTransactionIcon | undefined;

  ngOnInit() {
    if (this.transaction === undefined) {
      throw new Error(`"transaction" input is required in "${this.constructor.name}"`);
    }

    this.transactionIcon = this.transactionsIconCategoriesFactory.create(this.transaction);
  }

  ngAfterViewInit() {
    if (this.categoryImage) {
      this.categoryImage.nativeElement.onload = () => {
        this.hasImageLoaded = true;
        this.cd.markForCheck();
      };
    }
  }

  constructor(
    private transactionsIconCategoriesFactory: TransactionsIconCategoriesFactoryService,
    private readonly cd: ChangeDetectorRef,
  ) {}
}
