import { ChangeDetectionStrategy, Component, Output, EventEmitter, Input, OnDestroy } from '@angular/core';
import { DomSanitizer, SafeStyle, SafeUrl } from '@angular/platform-browser';
import { NavigationState } from '../../../model/UI/document-viewer-commons';

interface ImageUIState {
  scale: number;
}

const ZOOM_FACTOR = 1.1;
const ZOOM_IN_THRESHOLD = 4;
const ZOOM_OUT_THRESHOLD = 0.1;

@Component({
  selector: 'bb-image-viewer',
  templateUrl: './image-viewer.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ImageViewerComponent implements OnDestroy {
  @Input() set source(src: string | undefined) {
    if (src) {
      this.revokeObjectURLIfNeeded();
      this.imageObjectUrl = src;

      this.imageObjectTrustedUrl = this.domSanitizer.bypassSecurityTrustResourceUrl(this.imageObjectUrl);
    }
  }
  @Input() navigationState: NavigationState = { currIdx: 0, total: 0 };
  @Output() navigate = new EventEmitter<number>();
  imageObjectTrustedUrl: SafeUrl | undefined;
  imageUiState: ImageUIState = { scale: 1 };

  /**
   * Internal
   */
  private imageObjectUrl: string | undefined;

  constructor(private readonly domSanitizer: DomSanitizer) {}

  get canZoomOut(): boolean {
    return this.imageUiState.scale >= ZOOM_OUT_THRESHOLD;
  }

  get canZoomIn(): boolean {
    return this.imageUiState.scale <= ZOOM_IN_THRESHOLD;
  }

  get scale(): SafeStyle {
    return this.domSanitizer.bypassSecurityTrustStyle(`scale(${this.imageUiState.scale})`);
  }

  ngOnDestroy() {
    this.revokeObjectURLIfNeeded();
  }

  zoomIn(): void {
    if (this.canZoomIn) {
      this.imageUiState.scale *= ZOOM_FACTOR;
    }
  }

  zoomOut(): void {
    if (this.canZoomOut) {
      this.imageUiState.scale /= ZOOM_FACTOR;
    }
  }

  private revokeObjectURLIfNeeded(): void {
    if (this.imageObjectUrl) {
      window.URL.revokeObjectURL(this.imageObjectUrl);
    }
  }
}
