import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { GetTransactionsRequestParams, TransactionItem } from '@backbase/data-ang/transactions';

import { Transaction } from './transaction.model';
import { TransactionsSortOptions } from './transactions-sort-options.model';
import {
  sortFieldsDirectionHeader,
  totalCountHeader,
  availableExportTypes,
  unvailableExportTypesReasons,
  BillingStatusEnum,
} from '../constants';
import { TransactionsExportOptions, UnavailableExportTypeReasons } from './transactions-export-options.model';
import { TransactionsListOptions } from './transactions-list-options.model';
import { TransactionsEnumValue } from './transactions-enum-value.model';

export interface TransactionsList {
  items: Transaction[];
  totalCount: number;
  sort?: TransactionsSortOptions;
  availableExportTypesHeader?: string;
  httpResponseError?: HttpErrorResponse;
  unavailableExportTypesReasonsHeader?: UnavailableExportTypeReasons;
  initialRequest?: boolean;
}
/* eslint-disable no-redeclare*/
export namespace TransactionsList {
  // eslint-disable-next-line complexity
  const processTransaction = (transaction: Transaction) => {
    const amountSign = transaction.creditDebitIndicator === 'DBIT' ? -1 : 1;
    const formattedTransaction: Transaction = {
      ...transaction,
      isPending: (function isPending(data) {
        let result = false;
        if (data && data.billingStatus) {
          const billingStatus = data.billingStatus.toUpperCase();
          result = billingStatus === BillingStatusEnum.pending || billingStatus === BillingStatusEnum.unbilled;
        }

        return result;
      })(transaction),
      ...(transaction.merchant
        ? {
            merchant: {
              id: transaction.merchant.id,
              name: transaction.merchant.name,
              ...(transaction.merchant.logo ? { logo: transaction.merchant.logo } : {}),
              ...(transaction.merchant.website ? { website: transaction.merchant.website } : {}),
            },
          }
        : {}),
    };

    if (transaction.transactionAmountCurrency) {
      const amountNumber = parseFloat(transaction.transactionAmountCurrency.amount);

      formattedTransaction.transactionAmountCurrency = {
        ...transaction.transactionAmountCurrency,
        unsignedAmount: amountNumber,
        amount: (amountSign * (amountNumber || 0)).toString(),
      };
    }

    if (transaction.instructedAmountCurrency) {
      const amountNumber = parseFloat(transaction.instructedAmountCurrency.amount);

      formattedTransaction.instructedAmountCurrency = {
        ...transaction.instructedAmountCurrency,
        unsignedAmount: amountNumber,
        amount: (amountSign * (amountNumber || 0)).toString(),
      };
    }

    formattedTransaction.currencyExchangeAvailability =
      !!transaction.instructedAmountCurrency &&
      !!transaction.transactionAmountCurrency &&
      !!transaction.currencyExchangeRate;

    return formattedTransaction;
  };

  const processTransactions = (transactionsList: TransactionsList) => ({
    ...transactionsList,
    items: transactionsList.items.map(processTransaction),
  });

  const processTotalCountHeader = (totalCount: string | null) => (totalCount ? parseInt(totalCount, 10) : 0);

  const processAvailableExportTypes = (availableExports: string | null) => availableExports || '';

  const processUnavailableExportTypesReasons = (unavilableExportTypesReasonsValue: string | null) =>
    JSON.parse(unavilableExportTypesReasonsValue || '{}');

  const processSortFieldsDirectionHeader = (
    sortFieldsDirection: string | null,
  ): TransactionsSortOptions | undefined => {
    if (sortFieldsDirection === null || !sortFieldsDirection.length) {
      return undefined;
    }

    const sortOption = sortFieldsDirection
      .split(',')[0]
      .trim()
      .replace(/\s{2,}/g, ' ');

    const [orderBy, direction] = sortOption.split(' ');

    if (!orderBy || (direction !== 'ASC' && direction !== 'DESC')) {
      throw new Error(
        `Invalid value for "initialSortOptions" property.
        Please use "<orderBy> <direction>" format, e.g., "bookingDate DESC",
        or leave it empty.`,
      );
    }

    return {
      orderBy,
      direction,
    };
  };

  function padValueWithZeros(value: number, size = 2) {
    return (value + '').padStart(size, '0');
  }

  function formatDate(date: string | undefined) {
    const safeDate = date || '';
    if (/^[0-9]{4}\-[0-9]{2}\-[0-9]{2}/.exec(safeDate)) {
      const localDate = new Date(safeDate);
      const month = padValueWithZeros(localDate.getMonth() + 1);
      const day = padValueWithZeros(localDate.getDate());
      const year = localDate.getFullYear();

      return `${year}-${month}-${day}`;
    } else if (date !== undefined) {
      console.error(`invalid date provided ${date}`);
    }

    return undefined;
  }

  export const fromHttpResponse = (response: HttpResponse<TransactionItem[]>): TransactionsList => {
    const totalCount = processTotalCountHeader(response.headers.get(totalCountHeader));
    const sort = processSortFieldsDirectionHeader(response.headers.get(sortFieldsDirectionHeader));
    const availableExportTypesHeader = processAvailableExportTypes(response.headers.get(availableExportTypes));
    const unavailableExportTypesReasonsHeader = processUnavailableExportTypesReasons(
      response.headers.get(unvailableExportTypesReasons),
    );

    return processTransactions({
      items: (response.body as Transaction[]) || [],
      totalCount,
      ...(sort ? { sort } : {}),
      availableExportTypesHeader,
      unavailableExportTypesReasonsHeader,
    });
  };

  export const concat = (transactionsList1: TransactionsList, transactionsList2: TransactionsList) => {
    if (transactionsList2.httpResponseError) {
      return {
        ...transactionsList1,
        items: [...transactionsList1.items],
        httpResponseError: transactionsList2.httpResponseError,
      };
    }

    return {
      items: [...transactionsList1.items, ...transactionsList2.items],
      totalCount: transactionsList1.totalCount,
      ...(transactionsList2.sort ? { sort: transactionsList2.sort } : {}),
      ...(transactionsList2.availableExportTypesHeader
        ? { availableExportTypesHeader: transactionsList2.availableExportTypesHeader }
        : {}),
      ...(transactionsList2.unavailableExportTypesReasonsHeader
        ? { unavailableExportTypesReasonsHeader: transactionsList2.unavailableExportTypesReasonsHeader }
        : {}),
      httpResponseError: transactionsList2.httpResponseError,
    };
  };

  export const hasLoadedAll = (transactionsList: TransactionsList) =>
    transactionsList.items.length === transactionsList.totalCount;

  export const toHttpRequest = (
    listOptions: TransactionsListOptions | TransactionsExportOptions,
  ): GetTransactionsRequestParams => {
    const turnIntoArrayObject = (value: any, name: any, fn: (param: any) => any) => {
      if (!value) {
        return {};
      }

      return Array.isArray(value) ? { [name]: fn(value) } : { [name]: [fn(value)] };
    };

    const optionalItem = <T>(name: string, value: T) => (value !== undefined ? { [name]: value } : {});

    const isTransactionListOption = (data: any): data is TransactionsListOptions =>
      Object.prototype.hasOwnProperty.call(data, 'pagination');

    const isTransactionExportOptions = (data: any): data is TransactionsExportOptions =>
      Object.prototype.hasOwnProperty.call(data, 'exportType');

    return {
      ...turnIntoArrayObject(listOptions.account.arrangementId, 'arrangementsIds', (value) => value),
      ...optionalItem('query', listOptions.search?.query),
      ...optionalItem('amountGreaterThan', listOptions.filter?.amount?.from),
      ...optionalItem('amountLessThan', listOptions.filter?.amount?.to),
      ...optionalItem(
        'bookingDateGreaterThan',
        listOptions.filter?.date?.from === '' ? undefined : formatDate(listOptions.filter?.date?.from),
      ),
      ...optionalItem(
        'bookingDateLessThan',
        listOptions.filter?.date?.to === '' ? undefined : formatDate(listOptions.filter?.date?.to),
      ),
      ...optionalItem('categories', listOptions.filter?.categories),
      ...optionalItem('fromCheckSerialNumber', listOptions.filter?.checkSerialNumber?.from),
      ...optionalItem('toCheckSerialNumber', listOptions.filter?.checkSerialNumber?.to),
      ...optionalItem('orderBy', listOptions.sort?.orderBy),
      ...optionalItem('direction', listOptions.sort?.direction),
      ...optionalItem('state', isTransactionListOption(listOptions) ? listOptions.state : undefined),
      ...optionalItem('from', isTransactionListOption(listOptions) ? listOptions.pagination?.from : undefined),
      ...optionalItem('size', isTransactionListOption(listOptions) ? listOptions.pagination?.size : undefined),
      ...optionalItem('billingStatus', listOptions.filter?.billingStatus?.value),
      ...optionalItem('creditDebitIndicator', listOptions.filter?.creditDebitIndicator?.value),
      ...optionalItem('exportType', isTransactionExportOptions(listOptions) ? listOptions.exportType : undefined),
      ...optionalItem('locale', isTransactionExportOptions(listOptions) ? listOptions.locale : undefined),
      ...turnIntoArrayObject(listOptions.filter?.types, 'types', (value: TransactionsEnumValue) => value.value),
    };
  };
}
/* eslint-enable no-redeclare*/
