import { HttpResponse } from '@angular/common/http';
import { ProductSummaryItem } from '@backbase/data-ang/arrangements';
import { Account } from './account.model';

export function fromArrangementsResponse(response: HttpResponse<ProductSummaryItem[]>): Account[] {
  return (response.body || []).map((item: ProductSummaryItem) => ({
    id: item.id,
    name: item.name,
    displayName: item.displayName,
    alias: item.bankAlias,
    IBAN: item.IBAN,
    BBAN: item.BBAN,
    currency: item.currency,
  }));
}
