import { Injectable } from '@angular/core';
import { AvailableFilters, AvailableFiltersConfig, ProductKind } from '../model/available-filters.model';
import { combineLatest, Observable, ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { defaultAvailableFiltersConfig } from '../constants';
import { ProductSummaryItem } from '@backbase/data-ang/arrangements';
import { AccountsService } from './accounts.service';

@Injectable()
export class AvailableFiltersConfigService {
  availableFilters$: Observable<AvailableFilters>;

  private productSummary$: Observable<ProductSummaryItem[]>;
  private readonly availableFiltersConfig$: ReplaySubject<AvailableFiltersConfig> =
    new ReplaySubject<AvailableFiltersConfig>(1);
  private readonly selectedAccounts$: ReplaySubject<string[]> = new ReplaySubject<string[]>(1);

  constructor(private readonly accountsService: AccountsService) {
    this.productSummary$ = this.getProductSummaryForSelectedAccounts();
    this.availableFilters$ = this.getAvailableFilters();
  }

  setFilterConfigurationAndAccount(
    availableFiltersConfig: Observable<AvailableFiltersConfig>,
    selectedAccounts: Observable<string[]>,
  ): void {
    availableFiltersConfig.subscribe(this.availableFiltersConfig$);
    selectedAccounts.subscribe(this.selectedAccounts$);
  }

  private getProductSummaryForSelectedAccounts(): Observable<ProductSummaryItem[]> {
    return combineLatest([this.accountsService.getProductSummary(), this.selectedAccounts$]).pipe(
      map(([productSummaryItems, selectedAccounts]) =>
        productSummaryItems.filter((productSummaryItem: ProductSummaryItem) =>
          selectedAccounts?.includes(productSummaryItem.id),
        ),
      ),
    );
  }

  private getAvailableFilters(): Observable<AvailableFilters> {
    return combineLatest([this.availableFiltersConfig$, this.productSummary$]).pipe(
      map(([availableFiltersConfig, productSummaryItems]) => {
        const availableFilters: AvailableFilters = productSummaryItems.reduce(
          (filters: AvailableFilters, productSummaryItem: ProductSummaryItem): AvailableFilters =>
            filters.concat(this.getFiltersFromConfigs(productSummaryItem, availableFiltersConfig)),
          [],
        );

        return Array.from(new Set(availableFilters));
      }),
    );
  }

  private getFiltersFromConfigs(
    productSummaryItem: ProductSummaryItem,
    availableFiltersConfig: AvailableFiltersConfig,
  ): AvailableFilters {
    const availableFilters: AvailableFilters | undefined =
      this.getFiltersFromConfigObject(availableFiltersConfig, productSummaryItem) ??
      this.getFiltersFromConfigObject(defaultAvailableFiltersConfig, productSummaryItem);

    return availableFilters ?? (defaultAvailableFiltersConfig.defaultFilters as AvailableFilters);
  }

  private getFiltersFromConfigObject(
    config: AvailableFiltersConfig,
    productSummaryItem: ProductSummaryItem,
  ): AvailableFilters | undefined {
    if (productSummaryItem.productKindName !== undefined) {
      const productKind: ProductKind | undefined = config[productSummaryItem.productKindName];

      if (productKind === undefined) return undefined;

      if (Array.isArray(productKind)) return productKind;

      if (productSummaryItem.productTypeName === undefined) return undefined;

      return productKind[productSummaryItem.productTypeName];
    }

    return undefined;
  }
}
