import { TransactionsExportTypeElement } from '../model/transactions-export-options.model';
import { Input, Output, EventEmitter, OnInit, Directive } from '@angular/core';
import { TransactionsFilterOptions } from '../model/transactions-filter-options.model';
import { AvailableFilters } from '../model/available-filters.model';

@Directive()
// eslint-disable-next-line
export class BaseTransactionsControls implements OnInit {
  /**
   * Whether a filter is applied on the transactions.
   */
  @Input() isFilterApplied = false;
  /**
   * Configurations for exporting transactions.
   */
  // eslint-disable-next-line
  @Input()
  exportConfig = [
    {
      name: 'CSV',
      isAvailable: true,
    },
    {
      name: 'OFX',
      isAvailable: true,
    },
  ];
  /**
   * Configuration parameter to show/hide filters
   */
  @Input() availableFilters: AvailableFilters = [];

  /**
   * EventEmitter for triggering a filter action.
   */
  @Output() readonly filter = new EventEmitter<TransactionsFilterOptions>();
  /**
   * EventEmitter for triggering a search action.
   */
  @Output() readonly search = new EventEmitter<string>();
  /**
   * EventEmitter for triggering an export action.
   */
  @Output() readonly export = new EventEmitter<TransactionsExportTypeElement>();
  /**
   * Whether the filter form is shown.
   */
  isFilterOpen = false;

  /**
   * Search event callback
   */
  onSearch(searchParam: string) {
    this.search.emit(searchParam);
  }
  /**
   * Filter event callback
   */
  onFilter(filterParams: TransactionsFilterOptions) {
    this.filter.emit(filterParams);
  }
  /**
   * Export event callback
   */
  onExport(type: TransactionsExportTypeElement) {
    this.export.emit(type);
  }
  /**
   * Close event callback to hide the filters controls
   */
  onClose() {
    this.isFilterOpen = false;
  }
  /**
   * Toogle method to hide or show the filter form
   */
  toggleFilter() {
    this.isFilterOpen = !this.isFilterOpen;
  }

  ngOnInit() {
    if (typeof this.exportConfig === 'undefined') {
      throw new Error(`"exportConfig" input is required in "${this.constructor.name}"`);
    }
  }
}
