import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import {
  TransactionCheckImageResponse,
  TransactionsCheckImageSide,
  TransactionsCheckImage,
} from '../../model/transactions-check-images.model';
import { Transaction } from '../../model/transaction.model';
import { TransactionsDetailsLoading, TransactionsDetailsLoadingState } from '../../model/transactions-details.model';

@Component({
  selector: 'bb-transaction-check-image',
  templateUrl: './transaction-check-image.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionCheckImageComponent {
  /**
   * The transaction object
   */
  @Input() transaction: Transaction | undefined;
  /**
   * The check image object.
   */
  // eslint-disable-next-line
  @Input()
  set checkImagesList(value: TransactionCheckImageResponse) {
    this.imageFront = value && value.images.find((data) => data.side === TransactionsCheckImageSide.FRONT);
    this.imageBack = value && value.images.find((data) => data.side === TransactionsCheckImageSide.BACK);
  }
  /**
   * The transaction details loading status.
   */
  @Input() transactionDetailsLoadingStatus: TransactionsDetailsLoading | undefined;
  readonly loadingStateEnum = TransactionsDetailsLoadingState;

  /**
   * Sides of the check after being proccesed by formatter class
   */
  imageFront: TransactionsCheckImage | undefined;
  imageBack: TransactionsCheckImage | undefined;

  /**
   * Index to display in the document viewer. For the purpose of hidding and showing
   * the viewer this will be base 1
   * when the value is 0 the document viewer will not be visible
   */
  index = 0;

  get hasErrors() {
    return (
      this.transactionDetailsLoadingStatus &&
      this.transactionDetailsLoadingStatus.checkImages &&
      this.transactionDetailsLoadingStatus.checkImages < 0
    );
  }

  get isDone() {
    return (
      this.transactionDetailsLoadingStatus &&
      this.transactionDetailsLoadingStatus.checkImages === TransactionsDetailsLoadingState.done
    );
  }

  onClose() {
    this.index = 0;
  }

  selectImage(index: number) {
    this.index = index;
  }
}
