import { Component, ViewChild, AfterViewInit, Input, ElementRef, ChangeDetectionStrategy } from '@angular/core';
import { TransactionLocation } from '@backbase/data-ang/transactions';

@Component({
  selector: 'bb-transactions-map-ui',
  templateUrl: 'transactions-map-ui.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsMapUiComponent implements AfterViewInit {
  /* eslint-disable */
  @ViewChild('bbMapContainer') mapContainer: ElementRef<HTMLElement> | undefined;
  /* eslint-enable */
  @Input() location: TransactionLocation | undefined;

  private buildMapOptions(location: TransactionLocation): google.maps.MapOptions | undefined {
    if (location.latitude && location.longitude) {
      return {
        center: {
          lat: location.latitude,
          lng: location.longitude,
        },
        zoom: 10,
        disableDefaultUI: true,
        zoomControl: false,
        styles: [
          {
            featureType: 'poi',
            stylers: [{ visibility: 'off' }],
          },
        ],
      };
    }

    return undefined;
  }

  getFullAddress(location: TransactionLocation): string {
    return encodeURI(`${location.latitude}, ${location.longitude}`);
  }

  ngAfterViewInit() {
    if (this.location && this.mapContainer) {
      const options = this.buildMapOptions(this.location);
      const map = new google.maps.Map(this.mapContainer.nativeElement, options);
      if (this.location && this.location.latitude && this.location.longitude) {
        const mapUrl = this.getFullAddress(this.location);
        const marker = new google.maps.Marker({
          position: {
            lat: this.location.latitude,
            lng: this.location.longitude,
          },
          map,
        });
        marker.addListener('click', () => {
          window.open('https://www.google.com/maps/dir//' + mapUrl, '_blank');
        });
      }
    }
  }
}
