import { TransactionsAccountOptions } from './transactions-account-options.model';
import { TransactionsFilterOptions } from './transactions-filter-options.model';
import { TransactionsSearchOptions } from './transactions-search-options.model';
import { TransactionsSortOptions } from './transactions-sort-options.model';

export interface TransactionsExportOptions {
  account: TransactionsAccountOptions;
  search?: TransactionsSearchOptions;
  filter?: TransactionsFilterOptions;
  sort?: TransactionsSortOptions;
  exportType: string;
  locale: string;
}

export interface TransactionsExportTypeElement {
  name: string;
  reason?: TransactionsExportErrorType;
  isAvailable: boolean;
}

export enum TransactionsExportErrorType {
  MIXED_PRODUCT_KINDS = 'MIXED_PRODUCT_KINDS',
  UNSUPPORTED_PRODUCT_KIND = 'UNSUPPORTED_PRODUCT_KIND',
  TOO_MANY_ITEMS = 'TOO_MANY_ITEMS',
  MULTIPLE_PRODUCTS = 'MULTIPLE_PRODUCTS',
  UNKNOWN_ERROR = 'UNKNOWN_ERROR',
}

export interface UnavailableExportTypeReasons {
  [reason: string]: TransactionsExportErrorType[];
}

export interface TransactionsExportConfig {
  availableExportTypesHeader?: string;
  unavailableExportTypesReasonsHeader?: UnavailableExportTypeReasons;
}

export function combineExportTypesConfig(exportConfig: TransactionsExportConfig): TransactionsExportTypeElement[] {
  let exportTypesArr = [];
  const { availableExportTypesHeader = '', unavailableExportTypesReasonsHeader } = exportConfig;
  const availableExportTypesArray = availableExportTypesHeader.split(',');
  const availableTypes = availableExportTypesArray
    .filter((name) => name !== '')
    .map((name: string) => ({
      name,
      reason: undefined,
      isAvailable: true,
    }));

  if (unavailableExportTypesReasonsHeader) {
    const unavailableTypes = Object.keys(unavailableExportTypesReasonsHeader).map((name) => {
      if (unavailableExportTypesReasonsHeader && Array.isArray(unavailableExportTypesReasonsHeader[name])) {
        return {
          name,
          reason: unavailableExportTypesReasonsHeader[name][0],
          isAvailable: false,
        };
      }

      return {
        name,
        reason: TransactionsExportErrorType.UNKNOWN_ERROR,
        isAvailable: false,
      };
    });

    exportTypesArr = [...availableTypes, ...unavailableTypes];
  } else {
    exportTypesArr = [...availableTypes];
  }

  return [...exportTypesArr].sort((left, right) => {
    if (left.name === right.name) {
      return 0;
    }

    return left.name > right.name ? 1 : -1;
  });
}
