export enum TransactionsDetailsLoadingState {
  error = -1,
  serverError = -2,
  badRequestError = -3,
  notFoundError = -4,
  loading = 0,
  done = 1,
  notLoaded = 2,
  notStarted = 3,
}

export enum DisputeStatusEnum {
  Refunded = 'REFUNDED',
  Undisputed = 'UNDISPUTED',
  Pending = 'PENDING',
  Rejected = 'REJECTED',
}

export type TransactionsDetailsLoadingType =
  | 'checkImages'
  | 'geolocation'
  | 'enableDisputeAndInquiry'
  | 'disputeOption'
  | 'batchOrder';

export type TransactionsDetailsLoading = Partial<
  Record<TransactionsDetailsLoadingType, TransactionsDetailsLoadingState>
>;
