import { map, pluck } from 'rxjs/operators';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { TransactionsFilterOptions } from '../model/transactions-filter-options.model';
import { TransactionsSortOptions } from '../model/transactions-sort-options.model';
import { WidgetPropertiesService } from '../services/widget-properties.service';
import { Input, Directive, Output, EventEmitter } from '@angular/core';
import { defaultAvailableFiltersConfig, deserialize, filterInputParameters, parseSortOptions } from '../constants';
import { AvailableFiltersConfig } from '../model/available-filters.model';
import { Transaction } from '../model/transaction.model';
import { TransactionsList } from '../model/transactions-list.model';

/* eslint-disable */
@Directive()
export class BaseTransactionsWidget {
  /**
   * API key needed for the google maps to work.
   */
  @Input() googleAPIKey: string | undefined;

  @Input('initialFilterOptions') set initialFilterOptionsValue(value: string) {
    const deseralizedValue = deserialize(value);
    this.initialFilterOptionsSubject.next(TransactionsFilterOptions.fromHttpGetParams(deseralizedValue));
  }
  @Input('initialSortOptions') set initialSortOptionsValue(value: string) {
    const options = parseSortOptions(value);
    this.initialSortOptionsSubject.next(options);
  }

  @Input() pageSize: number | undefined;
  @Input() maxNavPages: number | undefined;
  @Input() paginationType: string | undefined;
  @Input() showControls: boolean | undefined;
  @Input() showChangeCategory: boolean | undefined;
  @Input() showPendingTransactionsOnTop: boolean | undefined;
  @Input() showCheckImage: boolean | undefined;
  @Input() showManageNotes: boolean | undefined;
  @Input() getOrPostEndpoints: string | undefined;
  @Input() enableDisputeAndInquiry: boolean = false;
  @Input() disputeByBillingStatus: boolean | undefined;
  @Input() disputeEligibilityDays: number | undefined;
  @Input() disputeTransactionTypes: string | undefined;
  @Input() inquireTopicId: string | undefined;
  @Input() disputeTopicId: string | undefined;
  @Input() availableFiltersConfig: AvailableFiltersConfig = defaultAvailableFiltersConfig;

  @Output() transactionSelected = new EventEmitter<Transaction>();
  @Output() transactionsLoaded = new EventEmitter<TransactionsList | null>();

  private readonly initialFilterOptionsSubject = new BehaviorSubject<TransactionsFilterOptions | undefined>(undefined);
  private readonly initialSortOptionsSubject = new BehaviorSubject<TransactionsSortOptions | undefined>(undefined);

  readonly selectedAccount = this.getRouteParam(this.route, 'selectedAccount').pipe(
    map((selectedAccount) => selectedAccount as string),
    map((selectedAccount) =>
      typeof selectedAccount !== 'undefined' && selectedAccount.includes(',')
        ? selectedAccount.split(',')
        : selectedAccount,
    ),
  );

  private getRouteParam(route: ActivatedRoute, param: string): Observable<string | null> {
    const paramValue = route.paramMap.pipe(pluck<ParamMap, string | null>('params', param));

    if (!route.parent) {
      return paramValue;
    }

    // Ensure that level that has actual value keeps it from being cleared.
    return combineLatest([paramValue, this.getRouteParam(route.parent, param)]).pipe(
      map((params) => params[0] || params[1]),
    );
  }

  get initialFilterOptions(): Observable<TransactionsFilterOptions> {
    return filterInputParameters(
      this.initialFilterOptionsSubject.asObservable(),
      this.widgetProperties.initialFilterOptions,
    );
  }

  get initialSortOptions(): Observable<TransactionsSortOptions | undefined> {
    return filterInputParameters(
      this.initialSortOptionsSubject.asObservable(),
      this.widgetProperties.initialSortOptions,
    );
  }

  constructor(protected readonly route: ActivatedRoute, protected readonly widgetProperties: WidgetPropertiesService) {}

  onDetailOpen(transaction: Transaction) {
    this.transactionSelected.emit(transaction);
  }

  onTransactionsLoaded(transactionsList: TransactionsList) {
    this.transactionsLoaded.emit(transactionsList);
  }
}
/* eslint-enable */
